<template>
  <div>
    <el-scrollbar class="scroll-container">
      <el-form ref="Form1" :model="form" :rules="rules" label-width="120px" label-position="right">
        <el-form-item label="所属区域：" prop="region">
          <el-select v-model="form.region" placeholder="请选择" filterable clearable style="width: 178px;">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
          </el-select>
        </el-form-item>
        <el-form-item label="看板名称：" prop="name">
          <el-input v-model="form.name" placeholder="请填写" clearable style="width: 178px;" />
        </el-form-item>
      </el-form>
      <el-form ref="Form2" :model="form" :rules="rules" label-width="120px" label-position="top">
        <el-form-item label="看板内容：请选择看板展示的图表（可多选）" prop="chartIds" label-width="320px">
          <el-checkbox-group v-model="form.chartIds">
            <el-checkbox v-for="(chartTemItem, index) in chartTems" :key="index" :border="true" :label="chartTemItem.id">{{ chartTemItem.name }}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </el-scrollbar>
    <div style="text-align: right;">
      <el-button @click="handlePreview">预 览</el-button>
      <el-button type="primary" @click="handleSave">保 存</el-button>
      <el-button @click="handleCancel">取 消</el-button>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'
import { getBoardDetailApi, getchartListApi, saveBoardApi } from '@/apis/modules/analysis/board'

const INIT_FORM = () => ({
  region: '', // 所属区域
  name: '', // 看板名称
  chartIds: [] // 看板内容
})
const checkRegion = (rule, value, callback) => {
  if (!value) {
    callback(new Error('请选择'))
  } else {
    callback()
  }
}
const checkName = (rule, value, callback) => {
  if (!value) {
    callback(new Error('请填写'))
  } else {
    callback()
  }
}
const checkChartIds = (rule, value, callback) => {
  if (!value.length) {
    callback(new Error('至少选择1个'))
  } else if (value.length > 9) {
    callback(new Error('最多选择9个'))
  } else {
    callback()
  }
}
export default {
  props: {
    // 编辑看板页面：看板ID
    id: {
      type: [String, Number],
      default: ''
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      options: [
        { label: '科技园', value: 1 },
        { label: '中院', value: 2 }
      ],
      chartTems: [],
      rules: {
        region: [
          { validator: checkRegion, trigger: 'change' }
        ],
        name: [
          { validator: checkName, trigger: 'blur' }
        ],
        chartIds: [
          { validator: checkChartIds, trigger: 'change' }
        ]
      }
    }
  },
  mounted() {
    // 当前为编辑看板页面
    this.id && this.initInfo()
    // 获取图表列表
    this.initChartList()
  },
  methods: {
    handlePreview() {
      Promise.all([
        this.$refs['Form1'].validate(),
        this.$refs['Form2'].validate()
      ])
        .then(() => {
          // 正常操作
          this.$router.push({
            name: 'PreviewKanban',
            params: {
              ids: this.form.chartIds.join()
            },
            query: {
              // 修改传参 新增不传
              ...this.id && { extraInfo: {
                ...this.$route.params
              }}
            }
          })
        })
        .catch(() => {
          // 错误操作
          console.log('error submit!!')
          return false
        })
    },
    initInfo() {
      const params = { id: this.id }
      return new Promise((resolve, reject) => {
        getBoardDetailApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.form = { ...data }
              this.$set(this.form, 'chartIds', data.chartTemplates.map(item => item.id))
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    initChartList() {
      const params = {}
      return new Promise((resolve, reject) => {
        getchartListApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.chartTems = [...data]
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    handleSave() {
      Promise.all([
        this.$refs['Form1'].validate(),
        this.$refs['Form2'].validate()
      ])
        .then(() => {
          // 正常操作
          const params = {
            entity: {
              ...this.id && { id: this.id },
              region: this.form.region, // 所属区域
              name: this.form.name, // 看板名称
              chartIds: this.form.chartIds.join() // 看板内容
            }
          }
          saveBoardApi(params).then(res => {
            const { code } = res
            if (!code) {
              this.id ? this.$notify.success({ title: '成功', message: '修改成功' }) : this.$notify.success({ title: '成功', message: '保存成功' })
              this.$store.dispatch('tagsView/delView', this.$route)
              this.$router.push({
                name: 'KanbanConfig'
              })
            }
          }).catch(err => {
            console.log('err', err)
          })
        })
        .catch(() => {
          // 错误操作
          console.log('error submit!!')
          return false
        })
    },
    handleCancel() {
      this.$store.dispatch('tagsView/delView', this.$route)
      this.$router.push({
        name: 'KanbanConfig'
      })
    },
    resetForm() {
      this.$refs['Form1'].resetFields()
      this.$refs['Form2'].resetFields()
    },
    // 获取分享图片 base64
    getShareImgBase64() {
      return new Promise((resolve) => {
        setTimeout(() => {
          // #capture 就是我们要获取截图对应的 DOM 元素选择器
          html2canvas(document.querySelector('#capture'), {
            useCORS: true, // 【重要】开启跨域配置
            scale: window.devicePixelRatio < 3 ? window.devicePixelRatio : 2,
            allowTaint: true // 允许跨域图片
          }).then((canvas) => {
            const imgData = canvas.toDataURL('image/jpeg', 1.0)
            this.imgData = imgData
            resolve(imgData)
          })
        }, 300) // 这里加上 300ms 的延迟是为了让 DOM 元素完全渲染完成后再进行图片的生成
      })
    },
    // 将base64转换为blob
    base64toBlob(dataurl) {
      const arr = dataurl.split(',')
      const mime = arr[0].match(/:(.*?);/)[1]
      const bstr = atob(arr[1])
      let n = bstr.length
      const u8arr = new Uint8Array(n)
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], { type: mime })
    },
    // 将blob转换为file
    blobToFile(theBlob, fileName) {
      theBlob.lastModifiedDate = new Date()
      theBlob.name = fileName
      return theBlob
    },
    // 调用这个方法，里面会调用上面的两个方法，最终得到一个blob类型的file文件
    changeBase2File(base64Data, fileName) {
      // 调用 dataURLtoBlob 和 blobToFile 得到一个file文件类型的图片
      //! 因为base64传输给后台读取速度太慢，所以再次转成file类型传给后台存储
      const blob = this.base64toBlob(base64Data)
      const file = this.blobToFile(blob, fileName)
      return file
    }
    // const fileData = this.changeBase2File(base64Data, Date.now() + '.png')
  }
}
</script>

<style scoped lang="scss">
.scroll-container {
    height: calc(100vh - 150px);
}
::v-deep .el-scrollbar {
    .el-scrollbar__wrap {
        overflow: hidden;
    }
    .el-scrollbar__bar {
        // height: 0;
        &.is-horizontal {
            height: 0;
        }
        &.is-vertical {
            width: 0;
        }
    }
}
</style>

